* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

body {
  padding: 20px;
  font-family: Arial, sans-serif;
}

header {
  text-align: center;
  margin-bottom: 20px;
}

h1 {
  font-size: 24px;
}

.search-form {
  margin-bottom: 20px;
  display: flex;
}

input[type="text"] {
  border: 1px solid #ccc;
  border-right: none;
  border-radius: 4px 0 0 4px;
  flex: 1;
  padding: 10px;
  font-size: 16px;
}

button[type="submit"] {
  color: #fff;
  cursor: pointer;
  background-color: #007bff;
  border: none;
  border-radius: 0 4px 4px 0;
  padding: 10px 20px;
  font-size: 16px;
}

.gallery {
  grid-gap: 20px;
  grid-template-columns: repeat(auto-fit, minmax(300px, 1fr));
  margin-bottom: 20px;
  display: grid;
}

.photo-card {
  border: 1px solid #ccc;
  border-radius: 4px;
  overflow: hidden;
}

.photo-card img {
  width: 100%;
  height: auto;
}

.info {
  padding: 10px;
}

.info-item {
  margin: 0;
}

.load-more {
  width: 100%;
  text-align: center;
  color: #fff;
  cursor: pointer;
  background-color: #007bff;
  border: none;
  border-radius: 4px;
  padding: 10px;
  font-size: 16px;
  display: block;
}

.load-more.hide {
  display: none;
}

.photo-card {
  border: 1px solid #ccc;
  border-radius: 4px;
  transition: transform .3s;
  overflow: hidden;
}

.photo-card:hover {
  cursor: pointer;
  transform: scale(1.05);
  box-shadow: 0 0 10px rgba(0, 0, 0, .3);
}

.photo-card:focus {
  outline: none;
  box-shadow: 0 0 10px rgba(0, 0, 0, .3);
}

.photo-card:active {
  transform: scale(.95);
}

